.homeDiv {
    min-height: 100vh;
}

.homeHeader {
    background-color: rgb(239, 14, 61);
    min-height: 50vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.homeTitle > img {
height: 6rem;
margin-top: 1rem;
}

.battleDescription {
    font-size: 1.5rem;
    font-weight: 500;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    height: 80%;
    color: rgb(56, 56, 56);
}

.battleButton {
    width: 100%;
    background-color: rgb(108, 199, 108);
    border: none;
    border-radius: 1rem;
    font-size: large;
    font-weight: bold;
    flex-grow: 1;
}

.link {
    height: 100%;
    width: 100%;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    color: black;
}

.battleButton:hover {
    cursor: pointer;
}

.trainersAndDescription {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
}

.trainerLeft, .trainerRight {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;
}

.trainerPokemon {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    width: 15rem;
    border-radius: 1rem;
    background-color: rgb(250, 248, 248);
}

.trainerPic {
    height: 12rem;
}

.battlePokemon {
    height: 3rem;
    mix-blend-mode: multiply;
}

.blackBar {
    height: 2rem;
    background-color: rgb(56, 56, 56);
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.bigCircle {
    height: 4rem;
    width: 4rem;
    background-color: rgb(238, 238, 238);
    border: .5rem solid rgb(56, 56, 56);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.lilCircle {
    height: 2rem;
    width: 2rem;
    background-color: rgb(238, 238, 238);
    border: .25rem solid rgb(56, 56, 56);
    border-radius: 50%;
}

.homeBody {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}

.leftArrow, .rightArrow {
    width: 5%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    border-radius: 1rem;
    font-size: x-large;
}

.leftArrow:before {
    content: "<";
}

.rightArrow:before {
    content: ">";
}

.leftArrow:hover, .rightArrow:hover {
    background-color: rgb(226, 217, 217);
    cursor: pointer;
}

.bodyMain {
    flex-grow: 1;
    display: flex;
    justify-content: center;    
    align-items: center;
    padding: 2rem;
    margin-top: 1rem;
    gap: 2%;
    flex-wrap: wrap;
}

.cardContainer {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    background-color: rgb(223, 224, 211);
    height: 12rem;
    width: 18%;
    border-radius: 1rem;
    margin-bottom: 1rem;
}

.card {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1px;
    background-color: rgb(211, 202, 202);
    height: 12rem;
    border-radius: 1rem;
}

.card > img {
    height: 8rem;
}

.stats {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    height: 90%;
    flex-grow: 1;
    padding: .5rem;
}

.pokemonStat {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: .6rem;
    width: 100%;
}

.statName {
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.progressBarContainer {
    width: 50%;
    background-color: rgb(199, 151, 151);
    height: 1rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border-radius: 2rem;
}

.progressBar {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 2rem;
}

.addToTeam {
    align-self: center;
    width: 70%;
    border: none; 
    border-radius: .5rem;
    font-weight: bold;
    color:black;
    background-color: rgb(245, 140, 198);
}

.addToTeam:hover {
    cursor: pointer;
}

.battleDescriptionTextMobileContainer {
    display: none;
}

@media screen and (max-width: 1750px) {
    .bodyMain {
        gap: 2%;
    }

    .cardContainer {
        width: 23%;
    }
}

@media screen and (max-width: 1400px) {
    .bodyMain {
        gap: 5%;
        padding: .5rem;
    }

    .cardContainer {
        width: 45%;
    }
}

@media screen and (max-width: 800px) {
    .bodyMain {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 1rem;
        margin-top: 1.5rem;
    }

    .cardContainer {
        width: 70%;
    }

    .trainersAndDescription {
        width: 95%;
    }

    .trainerLeft, .trainerRight, .battleDescription {
        width: 33%;
    }

    .leftArrow, .rightArrow {
        align-self:flex-end;
        height: 4rem;
        width: 10%;
        background-color: rgb(211, 202, 202);
        font-size: medium;
    }

    .leftArrow:before {
        content: "";
    }

    .rightArrow:before {
        content: "";
    }

    .leftArrow:after {
        content: "Last Page";
    }

    .rightArrow:after {
        content: "Next Page";
    }
}

@media screen and (max-width: 650px) {
    .homeTitle > img {
        width: 80%;
    }

    .homeTitle {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .trainerPokemon {
        width: 100%;
        gap: 0;
        justify-content: space-around;
    }

    .trainerLeft, .trainerRight {
        width: 45%;
    }

    .trainerPokemon {
        width: 80%;
    }

    .trainerPokemon > img {
        width: 29%;
    }

    .card > img {
        height: 50%;
    }

    .battleDescription {
        display: none;
    }

    .battleDescriptionTextMobileContainer {
        display: flex;
        flex-direction: column;
        height: 6rem;
        justify-content: center;
        align-items: center;
        font-size: large;
        text-align: center;
        color: rgb(56, 56, 56);
    }

    .battleDescriptionTextMobile {
        height: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .battleButton {
        margin-bottom: 2rem;
    }

    .bodyMain {
        width: 100%;
    }

    .cardContainer {
        width: 100%;
    }
}

@media screen and (max-width: 500px) {
    .trainerPokemon {
        width: 100%;
    }

    .leftArrow, .rightArrow {
        font-size: small;
        height: 3rem;
    }
}
